@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  padding: 1.25em;
  min-height: calc(100vh - 2.5em);
  background: linear-gradient(110deg, #fdcd3b 60%, #ffed4b 60%);
}

.canvasWrapper {
  border: 4px solid #18191a;
  border-radius: 20px;
  background-color: #37474f;
  opacity: 1;
  background-image: linear-gradient(to bottom, transparent 50%, #583c87 50%),
    linear-gradient(to right, #5e428e 50%, #583c87 50%);
  background-size:
    36px 36px,
    36px 36px;
}

.canvasWrapperGameEnd {
  border: 4px solid #18191a;
  border-radius: 20px;
  background-color: #37474f;
  opacity: 0.9;
  background-image: linear-gradient(to bottom, transparent 50%, #f36363 50%),
    linear-gradient(to right, #ef4a4a 50%, #f36363 50%);
  background-size: 36px 36px;
}

.mainBoardWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.gameScreenWrapper {
  display: flex;
  flex-direction: row;
  grid-gap: 4em;
  gap: 4em;
  justify-content: center;
  align-items: center;
}

.mainCanvasWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  border: 2px solid #42a5f5;
  border-radius: 8px;
  padding: 1.5em;
  width: 600px;
  line-height: 1.5em;
}

.menu-title {
  font-size: 2em;
  font-weight: bold;
  color: #18191a;
  text-align: center;
}

.header {
  display: flex;
  justify-content: space-between;
  align-self: stretch;
  align-items: stretch;
  padding-left: 0;
  padding-right: 0;
  font-size: 1.2rem;
  color: #fff;
  grid-gap: 1em;
  gap: 1em;
}

.scoreWrapper,
.walletInfoWrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 0.02em;
  gap: 0.02em;
  font-size: 2rem;
  font-weight: 700;
  padding: 0.5em;
  width: 100%;

  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 8px;
}

.scoreTitle,
.walletTitle {
  font-size: 1rem;
  color: #18191a;
}

.contentWrapper {
  display: flex;
  align-items: stretch;
  grid-gap: 1em;
  gap: 1em;
}

.rankingBoardWrapper {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  width: 13em;
  grid-gap: 2em;
  gap: 2em;
  /* border: 1px solid #42a5f5; */
  border-radius: 20px;
  padding: 1.5em;
  line-height: 1.5em;

  font-weight: 700;
  color: #18191a;

  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.rankingContentWrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5em;
  gap: 0.5em;

  font-weight: 400;
  color: #18191a;
}

.rankCard {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px;
  padding: 1em;
  color: #f99417;
  font-weight: 600;

  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 8px;
}

.rankInfo {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.avatar {
  border-radius: 4px;
}

.startScreenModal {
}

.modalWrapper {
  width: 28em;
  color: #000;
  padding: 20px;
  border-radius: 14px;
  background-color: #fafafa;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.modalContentWrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
}

.gameModalWrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: auto;
  color: #000;
  padding: 20px;
  border-radius: 14px;
  background-color: #fafafa;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.gameOverTitle {
  font-size: 2em;
  font-weight: bold;
  color: #f36363;
}

.gameOverScoreWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.gameOverScoreTitle {
  font: 1rem;
  color: #18191a;
  font-weight: 700;
}

.gameOverScore {
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
}

body {
  margin: 0;
  font-family:
    'Play',
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

